</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'orders',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        fetchUrl: '/api/order',
        useParamsId: true,
        cancelOrderModalVisible: false,
        changeStatusModalVisible: false,
        confirmed: [],
        unconfirmed: [],
        archived: [],
      }
    },

    methods: {
      prepareData(data) {
         if (!data) return;

         this.confirmed = data.confirmed;
         this.unconfirmed = data.unconfirmed;
         this.archived = data.archived;
      },

      closeCancelOrderModal() {
        this.cancelOrderModalVisible = false;
      },

      closeChangeStatusModal() {
        this.changeStatusModalVisible = false;
      }
    },

    mounted() {
      this.setTitle('Заказ №123456');
      this.setBackUrl('/orders');
    }
  }
</script>

