import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const global = {
    namespaced: true,
    state: {
      orderStatuses: []
    },
    mutations: {
      setOrderStatuses(state, data) {
        state.orderStatuses = Object.assign(data, state.orderStatuses);
      },
    },
    getters: {
      orderStatuses(state) {
        return state.orderStatuses;
      }
    }
};

const store = new Vuex.Store({
    modules: { global }
});

export default {
    store,
    install (Vue, options) {
        Vue.prototype.$store = store
    }
}


