<template>
  <div id="app" :class="{'bg-white': layout.whiteBg}" v-device-touch>
      <c-header v-show="!layout.rawTemplate"
                :title="title"
                :backUrl="backUrl"
                :homePage="layout.homePage"
                :enableSearch="layout.enableHeaderSearch"/>
    <main role="main">
        <router-view/>
    </main>
  </div>
</template>

<script>
import FetchHelper from './mixins/FetchHelper.js';
import RouteHelper from './mixins/RouteHelper.js';

export default {
  name: 'app',
  mixins: [FetchHelper, RouteHelper],
  data() {
    return {
      fetchUrl: '/api/config',
      authorizationToken: '',
    };
  },
  computed: {

  },
  methods: {
    getAuthorizationToken() {
        this.authorizationToken = this.$route.query.token;
    },
    prepareData(data) {
        if (!data) return;

        this.$store.commit('global/setOrderStatuses', data.orderStatuses);
    },
  },
  beforeMount() {
    this.getAuthorizationToken();
  },
}
</script>

<style lang="scss">
  @import 'bootconf.scss';
  @import '~bootstrap/scss/bootstrap';

  #app {
      min-height: 100vh;
  }
</style>


