<template>
    <div class="ordersPage">
        <b-tabs nav-class="h4 text-uppercase bg-dark justify-content-center" nav-wrapper-class="stretched" content-class="d-lg-flex">
            <b-tab title="В работе" active class="col-lg-8 p-0 pb-4 overflow-auto">
                <b-container fluid>
                    <div class="p-4 text-center" v-if="!unconfirmed.length && !confirmed.length">
                        <span class="text-muted h4">Актуальные заказы отсутствуют</span>
                    </div>
                    <div class="mb-2 mt-4" v-if="unconfirmed.length">
                        <span class="small text-muted h4 text-uppercase">Новые</span>
                        <b-badge pill variant="danger" class="ml-1">{{ unconfirmed.length }}</b-badge>
                    </div>
                    <div class="bg-white rounded shadow-sm ml-n mr-n m-md-0" v-if="unconfirmed.length">
                        <ul class="list-unstyled pl-3 pl-lg-0">
                            <li class="pt-3 pr-3 pb-3 pl-md-3 position-relative"
                                v-for="(item, index) in unconfirmed"
                                :class="{'border-bottom': (index != unconfirmed.length -1), 'bg-secondary': item.id === selectedOrder}">
                                <div class="d-md-none">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h4 text-muted m-0">№{{ item.id }}</div>
                                        <div class="h4 text-muted m-0">{{ item.date }}</div>
                                    </div>
                                    <div class="h3 mb-1">{{ item.phone }} · {{ item.name }}</div>
                                    <div class="text-danger small strong">необходимо подтверждение</div>
                                </div>
                                <div class="d-none d-md-block">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h3 m-0">№{{ item.id }}</div>
                                        <div class="h3 m-0">{{ item.phone }} · {{ item.name }}</div>
                                        <div class="h3 m-0">{{ item.date }}</div>
                                    </div>
                                    <div class="text-danger small strong">необходимо подтверждение</div>
                                </div>
                                <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                             tag="div"
                                             class="d-lg-none block-link">
                                </router-link>
                                <b-link class="block-link d-none d-lg-block" @click="loadOrder(item.id)"></b-link>
                            </li>
                        </ul>
                    </div>
                    <div class="mb-2 mt-4" v-if="confirmed.length">
                        <span class="small text-muted h4 text-uppercase">Подтвержденные</span>
                        <b-badge pill variant="success" class="ml-1">{{ confirmed.length }}</b-badge>
                    </div>
                    <div class="bg-white rounded shadow-sm border-bottom ml-n mr-n m-md-0" v-if="confirmed.length">
                        <ul class="list-unstyled pl-3 pl-lg-0">
                            <li class="pt-3 pr-3 pb-3 pl-md-3 position-relative"
                                v-for="(item, index) in confirmed"
                                :class="{'border-bottom': (index != confirmed.length -1), 'bg-secondary': item.id === selectedOrder}">
                                <div class="d-md-none">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h4 text-muted m-0">№{{ item.id }}</div>
                                        <div class="h4 text-muted m-0">{{ item.date }}</div>
                                    </div>
                                    <div class="h3 m-0">{{ item.phone }} · {{ item.name }}</div>
                                </div>
                                <div class="d-none d-md-block">
                                    <div class="d-flex justify-content-between">
                                        <div class="h3 m-0">№{{ item.id }}</div>
                                        <div class="h3 m-0">{{ item.phone }} · {{ item.name }}</div>
                                        <div class="h3 m-0">{{ item.date }}</div>
                                    </div>
                                </div>
                                <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                             tag="div"
                                             class="d-lg-none block-link">
                                </router-link>
                                <b-link class="block-link d-none d-lg-block" @click="loadOrder(item.id)"></b-link>
                            </li>
                        </ul>
                    </div>
                </b-container>
            </b-tab>
            <b-tab title="Архив" class="col-lg-8 p-0 pb-4 overflow-auto">
                <b-container fluid>
                    <div class="p-4 text-center" v-if="!archived.length">
                        <span class="text-muted h4">Заказы в архиве отсутствуют</span>
                    </div>
                    <div class="bg-white rounded shadow-sm border-bottom ml-n mr-n mr-md-0 ml-md-0 mt-4" v-if="archived.length">
                        <ul class="list-unstyled pl-3 pl-lg-0">
                            <li class="pt-3 pr-3 pb-3 pl-md-3 position-relative"
                                v-for="(item, index) in archived"
                                :class="{'border-bottom': (index != archived.length -1), 'bg-secondary': item.id === selectedOrder}">
                                <div class="d-md-none">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h4 text-muted m-0">№{{ item.id }}</div>
                                        <div class="h4 text-muted m-0">{{ item.date }}</div>
                                    </div>
                                    <div class="h3 m-0">{{ item.phone }} · {{ item.name }}</div>
                                </div>
                                <div class="d-none d-md-block">
                                    <div class="d-flex justify-content-between mb-1">
                                        <div class="h3 m-0">№{{ item.id }}</div>
                                        <div class="h3 m-0">{{ item.phone }} · {{ item.name }}</div>
                                        <div class="h3 m-0">{{ item.date }}</div>
                                    </div>
                                </div>
                                <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                             tag="div"
                                             class="d-lg-none block-link">
                                </router-link>
                                <b-link class="block-link d-none d-lg-block" @click="loadOrder(item.id)"></b-link>
                            </li>
                        </ul>
                    </div>
                </b-container>
            </b-tab>
            <div class="col-lg-4 p-0 bg-secondary border-left d-none d-lg-flex align-items-center justify-content-center" v-if="!selectedOrder">
                <div class="h4 text-muted m-0">заказ не выбран</div>
            </div>
            <div class="col-lg-4 p-0 pt-5 pb-4 bg-secondary border-left d-none d-lg-block overflow-auto" v-if="selectedOrder">
                <b-container fluid>
                    <div class="h2 text-muted mb-5 strong">Заказ №645283</div>
                    <div class="mb-2 mt-4">
                        <span class="small text-muted h4 text-uppercase">Детали заказа</span>
                    </div>
                </b-container>
                <div class="bg-white border-bottom pt-4 pb-4 pl-4 pr-4">
                    <div class="mb-4">
                        <b-dropdown variant="info" text="Изменить статус" no-caret right class="float-right">
                            <b-dropdown-item @click="changeStatusModalVisible = !changeStatusModalVisible">Новый</b-dropdown-item>
                            <b-dropdown-item active disabled>Подтверждён</b-dropdown-item>
                            <b-dropdown-item @click="changeStatusModalVisible = !changeStatusModalVisible">Готов</b-dropdown-item>
                        </b-dropdown>
                        <div class="h4 mb-1 text-muted">Статус заказа</div>
                        <div class="h3 m-0">Подтвержден</div>
                    </div>
                    <div class="mb-4">
                        <div class="h4 mb-1 text-muted">Информация о заказчике</div>
                        <div class="h3 m-0">+ 7 909 876-54-32 · Ксения</div>
                    </div>
                    <div>
                        <div class="h4 mb-1 text-muted">Дата и время</div>
                        <div class="h3 m-0">Вчера, 22:45</div>
                    </div>
                </div>
                <b-container fluid>
                    <div class="mb-2 mt-4">
                        <span class="small text-muted h4 text-uppercase">12 позиций на сумму 3 456 ₽</span>
                    </div>
                </b-container>
                <div class="bg-white border-bottom pt-1 pb-1 pl-4 pr-4">
                    <ul class="list-unstyled">
                        <li class="pt-2 pb-2">
                            <div class="h3 mb-1">Пицца «Для друзей»</div>
                            <div class="d-flex justify-content-between">
                                <div class="h4 text-muted m-0">650 ₽ × 1 шт.</div>
                                <div class="h4 text-muted m-0">650 ₽</div>
                            </div>
                        </li>
                        <li class="pt-2 pb-2">
                            <div class="h3 mb-1">Пицца «Для друзей»</div>
                            <div class="d-flex justify-content-between">
                                <div class="h4 text-muted m-0">650 ₽ × 1 шт.</div>
                                <div class="h4 text-muted m-0">650 ₽</div>
                            </div>
                        </li>
                        <li class="pt-2 pb-2">
                            <div class="h3 mb-1">Пицца «Для друзей»</div>
                            <div class="d-flex justify-content-between">
                                <div class="h4 text-muted m-0">650 ₽ × 1 шт.</div>
                                <div class="h4 text-muted m-0">650 ₽</div>
                            </div>
                        </li>
                    </ul>
                </div>

                <div class="pt-5 text-center">
                    <b-link class="text-uppercase h4 strong text-danger" @click="cancelOrderModalVisible = !cancelOrderModalVisible">
                        <c-icon name="cancel" class="align-middle"/>
                        <span class="align-middle">Отменить заказ</span>
                    </b-link>
                </div>
            </div>
        </b-tabs>
        <b-modal v-model="cancelOrderModalVisible" hide-header-close centered>
            <div slot="modal-header">
                <div class="h2 strong">Отменить этот заказ?</div>
            </div>
            <div class="h3 text-muted">Это действие необратимо. Вы уверены, что хотите отменить заказ?</div>
            <div slot="modal-footer">
                <b-button variant="info" @click="closeCancelOrderModal">Нет</b-button>
                <b-button variant="danger" @click="closeCancelOrderModal">Отменить заказ</b-button>
            </div>
        </b-modal>
        <b-modal v-model="changeStatusModalVisible" hide-header-close centered>
            <div slot="modal-header">
                <div class="h2 strong">Изменить статус?</div>
            </div>
            <div class="h3 text-muted">Изменить статус этого заказа на «Готово»?</div>
            <div slot="modal-footer">
                <b-button variant="info" @click="closeChangeStatusModal">Отмена</b-button>
                <b-button variant="danger" @click="closeChangeStatusModal">Изменить статус</b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'orders',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        fetchUrl: '/api/orders',

        cancelOrderModalVisible: false,
        changeStatusModalVisible: false,
        confirmed: [],
        unconfirmed: [],
        archived: [],
        selectedOrder: null
      }
    },

    methods: {
      prepareData(data) {
        if (!data) return;

        data.forEach(item => {
          if (item.status === 0) {
            this.unconfirmed.push(item);
          }

          if (item.status === 1) {
            this.confirmed.push(item);
          }

          if (item.status === 2) {
            this.archived.push(item);
          }
        });
      },

      loadOrder(id) {
        this.selectedOrder = id;
      },

      closeCancelOrderModal() {
        this.cancelOrderModalVisible = false;
      },

      closeChangeStatusModal() {
        this.changeStatusModalVisible = false;
      }
    },

    mounted() {
      this.setTitle('Заказы');
      this.enableHeaderSearch();
    }
  }
</script>

<style lang="scss">
    @import '../../bootconf.scss';

    .ordersPage {
        .tabs {
            display: flex;
            flex-direction: column;
            height: calc(100vh - 56px);

            @media (min-width: map-get($grid-breakpoints, md)) {
                height: calc(100vh - 64px);
            }

            & .tab-content {
                flex: 1;
            }
        }

        .bg-white li:hover {
            background-color: rgba(153, 153, 153, .1);
        }

        li {
            transition: background-color .3s ease;
        }
    }
</style>


